@import "../components/variables";
@import "../components/mixin";

body {
  .slider{
    width: 100%;
    z-index: 0;
    img{
      width: 100%;
      max-height: 750px;
    }
  }
  .header__main{
    width: 100%;
    position: absolute;
    top: 0;
    .header{
      .container{
        .header__logo{
          a{
            display: none;
          }
          span{
            display: block;
          }
        }
      }
    }
  }
  .main {
    .containerParent();
    .container {
      .containerChild();
      .content__actualProduct,
      .content__saleProduct {
        &__headline {
          margin: 10px 0;
          display: flex;
          justify-content: space-between;
          align-items: baseline;
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
            white-space: nowrap;
          }
          div {
            display: flex;
            align-items: flex-end;
            margin: 0 40px 0 0;
            text-align: right;
            p {
              margin: 0 10px 10px 0;
              display: flex;
              font: .875em/1em @sansregular;
              white-space: pre-wrap;
              span {
                display: inline-block;
                width: 130px;
                height: 25px;
                margin: 5px 5px 0;
                font: 1em/25px @sansregular;
                text-align: center;
                border: 1px solid @mainColor;
                border-radius: 5px;
                &:hover {
                  cursor: pointer;
                  color: #fff;
                  background-color: @mainColor;
                }
              }
              .active{
                color: #fff;
                background-color: @mainColor;
              }
            }
          }
        }
        &__slider{
          display: flex;
          img {
            width: 15%;
            height: 350px;
            border-radius: 5px;
          }
          .slider {
            width: 85%;
            height: 350px;
            padding: 0 30px;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            position: relative;
            ul {
              height: 400px;
              li {
                margin: 0 10px;
                width: 225px;
                height: 350px;
                text-align: center;
                background-color: #fff;
                border: 1px solid #e6e6e6;
                border-radius: 5px;
                box-sizing: border-box;
                a {
                  display: block;
                  text-decoration: none;
                  img {
                    margin-top: 15px;
                    width: 200px;
                    height: 200px;
                  }
                  p {
                    padding: 10px;
                    font: 1em/1.125em @sansregular;
                    color: @linkColor;
                    text-align: left;
                  }
                  .price {
                    font: 1.25em/1.375em @sansbold;
                    span {
                      font: .875em/1em @sansregular
                    }
                  }
                }
                .hover{
                  margin: 0;
                  padding: 0;
                  width: 223px;
                  height: 50px;
                  display: none;
                  flex-direction: row-reverse;
                  justify-content: center;
                  align-items: flex-end;
                  position: absolute;
                  bottom: 10px;
                  z-index: 2;
                  background-color: #fff;
                  .addBusket,
                  .addFavorite{
                    margin: 25px 5px 10px;
                    height: 40px;
                    display: block;
                    position: relative;
                    font: 18px/40px @sansregular;
                    border: none;
                    border-radius: 5px;
                    outline: none;
                    z-index: 1;
                    &:hover{
                      cursor: pointer;
                    }
                  }
                  .addBusket{
                    width: 150px;
                    color: #fff;
                    background-color: @mainColor;
                    span{
                      margin: 0 0 0 10px;
                    }
                  }
                  .addFavorite{
                    width: 40px;
                    color: @linkColor;
                    background-color: #fff;
                    border: 1px solid @linkColor;
                    box-sizing: border-box;
                  }
                }
                &:hover {
                  height: 400px;
                  cursor: pointer;
                  .hover{
                    display: flex;
                    cursor: default;
                  }
                }
              }
            }
            .center {
              a {
                position: absolute;
                top: 40%;
                height: 100px;
                text-align: center;
                text-decoration: none;
                z-index: 5;
                border: 1px solid #e6e6e6;
                border-radius: 10px;
                background-image: none;
                i{
                  font-size: 2em;
                  line-height: 100px;
                  color: @fontColor;
                }
                &:hover{
                  border-color: @fontColor;
                  transition: .5s;
                }
              }
              .prevActual,
              .prevSale{
                left: 10px;
              }
              .nextActual,
              .nextSale{
                right: 10px;
              }
            }
          }
        }
        &__showMore {
          display: flex;
          justify-content: flex-end;
          a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 225px;
            height: 40px;
            margin: 15px 50px;
            font: 1em/40px @sansregular;
            color: #176975;
            text-decoration: none;
            background-color: transparent;
            border: 1px solid #176975;
            border-radius: 5px;
            i{
              font-size: 1.25em;
              line-height: 40px;
            }
            &:hover {
              color: #fff;
              background-color: #176975;
              cursor: pointer;
              transition: .5s;
            }
          }
        }
      }
    }
  }
  .footer {
    background-color: @hoverColor;
    .containerParent();
    .container {
      .containerChild();
      padding: 35px 0 40px;
      display: flex;
      flex-direction: column;
      p {
        padding: 5px 0;
        font: 1em/1.125em @sansregular;
        color: #fff;
      }
    }
  }
}
