.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
.containerChild {
  grid-area: container;
}
body .slider {
  width: 100%;
  z-index: 0;
}
body .slider img {
  width: 100%;
  max-height: 750px;
}
body .header__main {
  width: 100%;
  position: absolute;
  top: 0;
}
body .header__main .header .container .header__logo a {
  display: none;
}
body .header__main .header .container .header__logo span {
  display: block;
}
body .main {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .main .container {
  grid-area: container;
}
body .main .container .content__actualProduct__headline,
body .main .container .content__saleProduct__headline {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
body .main .container .content__actualProduct__headline h5,
body .main .container .content__saleProduct__headline h5 {
  font: 2.25em/2.375em "PTSerif-Italic";
  color: #0e3a40;
  white-space: nowrap;
}
body .main .container .content__actualProduct__headline div,
body .main .container .content__saleProduct__headline div {
  display: flex;
  align-items: flex-end;
  margin: 0 40px 0 0;
  text-align: right;
}
body .main .container .content__actualProduct__headline div p,
body .main .container .content__saleProduct__headline div p {
  margin: 0 10px 10px 0;
  display: flex;
  font: 0.875em/1em "PTSans-Regular";
  white-space: pre-wrap;
}
body .main .container .content__actualProduct__headline div p span,
body .main .container .content__saleProduct__headline div p span {
  display: inline-block;
  width: 130px;
  height: 25px;
  margin: 5px 5px 0;
  font: 1em/25px "PTSans-Regular";
  text-align: center;
  border: 1px solid #00929e;
  border-radius: 5px;
}
body .main .container .content__actualProduct__headline div p span:hover,
body .main .container .content__saleProduct__headline div p span:hover {
  cursor: pointer;
  color: #fff;
  background-color: #00929e;
}
body .main .container .content__actualProduct__headline div p .active,
body .main .container .content__saleProduct__headline div p .active {
  color: #fff;
  background-color: #00929e;
}
body .main .container .content__actualProduct__slider,
body .main .container .content__saleProduct__slider {
  display: flex;
}
body .main .container .content__actualProduct__slider img,
body .main .container .content__saleProduct__slider img {
  width: 15%;
  height: 350px;
  border-radius: 5px;
}
body .main .container .content__actualProduct__slider .slider,
body .main .container .content__saleProduct__slider .slider {
  width: 85%;
  height: 350px;
  padding: 0 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
}
body .main .container .content__actualProduct__slider .slider ul,
body .main .container .content__saleProduct__slider .slider ul {
  height: 400px;
}
body .main .container .content__actualProduct__slider .slider ul li,
body .main .container .content__saleProduct__slider .slider ul li {
  margin: 0 10px;
  width: 225px;
  height: 350px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  box-sizing: border-box;
}
body .main .container .content__actualProduct__slider .slider ul li a,
body .main .container .content__saleProduct__slider .slider ul li a {
  display: block;
  text-decoration: none;
}
body .main .container .content__actualProduct__slider .slider ul li a img,
body .main .container .content__saleProduct__slider .slider ul li a img {
  margin-top: 15px;
  width: 200px;
  height: 200px;
}
body .main .container .content__actualProduct__slider .slider ul li a p,
body .main .container .content__saleProduct__slider .slider ul li a p {
  padding: 10px;
  font: 1em/1.125em "PTSans-Regular";
  color: #0e3a40;
  text-align: left;
}
body .main .container .content__actualProduct__slider .slider ul li a .price,
body .main .container .content__saleProduct__slider .slider ul li a .price {
  font: 1.25em/1.375em "PTSans-Bold";
}
body .main .container .content__actualProduct__slider .slider ul li a .price span,
body .main .container .content__saleProduct__slider .slider ul li a .price span {
  font: 0.875em/1em "PTSans-Regular";
}
body .main .container .content__actualProduct__slider .slider ul li .hover,
body .main .container .content__saleProduct__slider .slider ul li .hover {
  margin: 0;
  padding: 0;
  width: 223px;
  height: 50px;
  display: none;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  bottom: 10px;
  z-index: 2;
  background-color: #fff;
}
body .main .container .content__actualProduct__slider .slider ul li .hover .addBusket,
body .main .container .content__saleProduct__slider .slider ul li .hover .addBusket,
body .main .container .content__actualProduct__slider .slider ul li .hover .addFavorite,
body .main .container .content__saleProduct__slider .slider ul li .hover .addFavorite {
  margin: 25px 5px 10px;
  height: 40px;
  display: block;
  position: relative;
  font: 18px/40px "PTSans-Regular";
  border: none;
  border-radius: 5px;
  outline: none;
  z-index: 1;
}
body .main .container .content__actualProduct__slider .slider ul li .hover .addBusket:hover,
body .main .container .content__saleProduct__slider .slider ul li .hover .addBusket:hover,
body .main .container .content__actualProduct__slider .slider ul li .hover .addFavorite:hover,
body .main .container .content__saleProduct__slider .slider ul li .hover .addFavorite:hover {
  cursor: pointer;
}
body .main .container .content__actualProduct__slider .slider ul li .hover .addBusket,
body .main .container .content__saleProduct__slider .slider ul li .hover .addBusket {
  width: 150px;
  color: #fff;
  background-color: #00929e;
}
body .main .container .content__actualProduct__slider .slider ul li .hover .addBusket span,
body .main .container .content__saleProduct__slider .slider ul li .hover .addBusket span {
  margin: 0 0 0 10px;
}
body .main .container .content__actualProduct__slider .slider ul li .hover .addFavorite,
body .main .container .content__saleProduct__slider .slider ul li .hover .addFavorite {
  width: 40px;
  color: #0e3a40;
  background-color: #fff;
  border: 1px solid #0e3a40;
  box-sizing: border-box;
}
body .main .container .content__actualProduct__slider .slider ul li:hover,
body .main .container .content__saleProduct__slider .slider ul li:hover {
  height: 400px;
  cursor: pointer;
}
body .main .container .content__actualProduct__slider .slider ul li:hover .hover,
body .main .container .content__saleProduct__slider .slider ul li:hover .hover {
  display: flex;
  cursor: default;
}
body .main .container .content__actualProduct__slider .slider .center a,
body .main .container .content__saleProduct__slider .slider .center a {
  position: absolute;
  top: 40%;
  height: 100px;
  text-align: center;
  text-decoration: none;
  z-index: 5;
  border: 1px solid #e6e6e6;
  border-radius: 10px;
  background-image: none;
}
body .main .container .content__actualProduct__slider .slider .center a i,
body .main .container .content__saleProduct__slider .slider .center a i {
  font-size: 2em;
  line-height: 100px;
  color: #176975;
}
body .main .container .content__actualProduct__slider .slider .center a:hover,
body .main .container .content__saleProduct__slider .slider .center a:hover {
  border-color: #176975;
  transition: .5s;
}
body .main .container .content__actualProduct__slider .slider .center .prevActual,
body .main .container .content__saleProduct__slider .slider .center .prevActual,
body .main .container .content__actualProduct__slider .slider .center .prevSale,
body .main .container .content__saleProduct__slider .slider .center .prevSale {
  left: 10px;
}
body .main .container .content__actualProduct__slider .slider .center .nextActual,
body .main .container .content__saleProduct__slider .slider .center .nextActual,
body .main .container .content__actualProduct__slider .slider .center .nextSale,
body .main .container .content__saleProduct__slider .slider .center .nextSale {
  right: 10px;
}
body .main .container .content__actualProduct__showMore,
body .main .container .content__saleProduct__showMore {
  display: flex;
  justify-content: flex-end;
}
body .main .container .content__actualProduct__showMore a,
body .main .container .content__saleProduct__showMore a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 225px;
  height: 40px;
  margin: 15px 50px;
  font: 1em/40px "PTSans-Regular";
  color: #176975;
  text-decoration: none;
  background-color: transparent;
  border: 1px solid #176975;
  border-radius: 5px;
}
body .main .container .content__actualProduct__showMore a i,
body .main .container .content__saleProduct__showMore a i {
  font-size: 1.25em;
  line-height: 40px;
}
body .main .container .content__actualProduct__showMore a:hover,
body .main .container .content__saleProduct__showMore a:hover {
  color: #fff;
  background-color: #176975;
  cursor: pointer;
  transition: .5s;
}
body .footer {
  background-color: #9e79ac;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .footer .container {
  grid-area: container;
  padding: 35px 0 40px;
  display: flex;
  flex-direction: column;
}
body .footer .container p {
  padding: 5px 0;
  font: 1em/1.125em "PTSans-Regular";
  color: #fff;
}
